import React from 'react';
import classnames from 'classnames';
import ContextMenu from '@/component/ContextMenu';
import Icon from '@/component/Icon';
import styles from './index.less';

type IProps = {
  list: any[];
  curItem: any;
  handleClickItem: any;
};

const Bar = ({
  list,
  curItem,
  handleClickItem,
}: IProps) => {
  const dropdownMenus: any[] = [
    // {
    //   name: '编辑',
    //   key: 'edit',
    // },
    // {
    //   name: '删除',
    //   key: 'delete',
    // },
  ];

  return (
    <div className={styles.bar}>
      <div className={styles.content}>
        <div className={styles.group}>
          {list.map((i, index) => {
            if (i.type === 'divider') {
              return <div key={i.index || index} className={styles.divider}></div>;
            } else {
              return (
                <ContextMenu menus={dropdownMenus} key={i.index}>
                  <div
                    className={classnames(styles.item, curItem.index === i.index && styles.active)}
                    onClick={() => handleClickItem(i)}
                  >
                    <Icon name={i.icon} />
                    <span className={styles.name}>{i.name}</span>
                  </div>
                </ContextMenu>
              );
            }
          })}
        </div>
      </div>
      {/* <div className={styles.divider}></div>
      <div className={styles.extra}>
        <div className={styles.group}>
            <Icon className={styles.item} name='PlusCircleOutlined' />
          </div>
      </div> */}
    </div>
  );
};

export default Bar;
