import React from 'react';
import { TAppItem } from '@/routes/type';
import App from './app';
import { appLayoutList } from './const';

type IProps = {
  item: TAppItem;
  onClick?: any;
  children?: any;
};

const Application = ({
  item,
  onClick,
  children,
}: IProps) => {
  const gotoUrl = (url: string) => { window.open(url); };
  return (
    <App item={item} onClick={() => (onClick && onClick(item)) || (item.url && gotoUrl(item.url))} children={children} />
  );
};

Application.appLayoutList = appLayoutList;

export default Application;
