import queryString from 'query-string';

interface IObjectAny {
  [propName: string]: any;
}

// 若传入 format='format' 则走规范
export const dateFormat = (date: (string | number | Date | null), format = 'yyyy-MM-dd hh:mm:ss TT'): string => {
  if (!date) {
    date = new Date();
  }
  if (typeof date === 'string' && /^\d+$/.test(date)) {
    date = new Date(+date);
  }
  if (typeof date === 'number') {
    date = new Date(date);
  }
  if (typeof date !== 'number' && !(date instanceof Date)) {
    date = date.replace(/年|月/g, '-').replace(/日/g, '');
    date = new Date(date);
  }

  const duration = Date.now() - date.getTime();
  const level1 = 60 * 1000; // 1 分钟
  const level2 = 60 * 60 * 1000; // 1 小时
  const level3 = 24 * 60 * 60 * 1000; // 1 天
  const level4 = 2 * 24 * 60 * 60 * 1000; // 2天

  if (format === 'default') {
    if (duration < level1) {
      return '刚刚';
    }
    if (duration >= level1 && duration < level2) {
      return `${Math.round(duration / level1)}分钟前`;
    }
    if (duration >= level2 && duration < level3) {
      return `${Math.round(duration / level2)}小时前`;
    }
    if (duration >= level3 && duration < level4) {
      format = '昨天 hh:mm';
    }
    // 判断是否过年了
    const _date = new Date();
    const _year = _date.getFullYear();
    if ((new Date(`${_year}-01-01`)).getTime() <= date.getTime()) {
      format = 'MM月dd日 hh:mm';
    } else {
      format = 'yyyy年MM月dd日 hh:mm';
    }
  }

  const o: IObjectAny = {
    'M+': date.getMonth() + 1, // 月份
    'd+': date.getDate(), // 日
    'h+': date.getHours(), // 小时 24进制
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
    S: date.getMilliseconds(), // 毫秒
    'T+': date.getHours() < 12 ? 'AM' : 'PM',
    'H+': date.getHours() % 12 === 0 ? 12 : date.getHours() % 12, // 小时 12进制
  };
  if (/(y+)/.test(format)) {
    format = format.replace(RegExp.$1, String(date.getFullYear()).substr(4 - RegExp.$1.length));
  }
  Object.keys(o).forEach(k => {
    if (new RegExp(`(${k})`).test(format)) {
      format = format.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? (o[k]) : `00${o[k]}`.substr(String(o[k]).length),
      );
    }
  });
  return format;
};

// 休眠 延迟
export const sleep = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

// 生成guid
export const guid = () => {
  return (
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    '-' +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) +
    (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1)
  );
};

/**
 * hex 转 rgb/rgba
 * return rgb/rgba
 */
export const hex2Rgba = (color: string, alpha?: number) => {
  let _color = color.toLowerCase();
  const reg = /^#([0-9a-fA-F]{3}|[0-9a-fA-F]{6})$/;
  if (reg.test(_color)) {
    _color = _color.replace(/#/, '');
    if (_color.length === 3) {
      let tempHex = '';
      for (let i = 0; i < _color.length; i++) {
        tempHex += _color[i] + _color[i];
      }
      _color = tempHex;
    }
    const r = parseInt(_color.substring(0, 2), 16);
    const g = parseInt(_color.substring(2, 4), 16);
    const b = parseInt(_color.substring(4, 6), 16);
    if (alpha !== undefined) {
      return `rgba(${r}, ${g}, ${b}, ${alpha})`;
    } else {
      return `rgb(${r}, ${g}, ${b})`;
    }
  }
  return null;
};

// 判断颜色深浅
export const isLightColor = (colorValue: string) => {
  if (colorValue.startsWith('#')) {
    colorValue = hex2Rgba(colorValue) || 'rgb(0,0,0)';
  }
  const colors = colorValue.replace('rgb(', '').replace('rgba(', '').replace(')', '').split(',');
  return +colors[0] * 0.299 + +colors[1] * 0.587 + +colors[2] * 0.114 >= 192;
};

export const getQuery = (param?: any) => {
  const url = param || window.location.href;
  return queryString.parseUrl(url).query;
};

// 对象转换为url参数
export const formateObjToParamStr = (paramObj: any = {}) => {
  return queryString.stringify(paramObj);
};
