import React from 'react';
import classnames from 'classnames';
import { message } from 'antd';
import ContextMenu from '@/component/ContextMenu';
import { TAppItem } from '@/routes/type';
import App from './App';
import Calendar from './Calendar';
import Notes from './Notes';
import Todo from './Todo';
import Blog from './Blog';
import GameFishingSingle from './GameFishingSingle';
import GameJzq from './GameJzq';
import GameXmlk from './GameXmlk';
import Game2048 from './Game2048';
import GameBallWeighing from './GameBallWeighing';
import CfsAnimate from './CfsAnimate';
import Noise from './Noise';
import { getSize } from './tool';
import styles from './index.less';
import TongJi from './TongJi';
import TianDiTu from './TianDiTu';
import Iframe from './Iframe';
import Files from './Files';
import TimerMark from './TimerMark';
import R3fDemo from './R3fDemo';

type IProps = {
  item: TAppItem;
  updateDate?: (action: string, item: TAppItem) => void;
};

const Widget = ({
  item,
  updateDate
}: IProps) => {
  const { w, h } = getSize(item.size);
  let appLayoutList = [
    '1x1', '1x2', '1x3', '1x4',
    '2x1', '2x2', '2x3', '2x4',
    '3x1', '3x2', '3x3', '3x4',
    '4x1', '4x2', '4x3', '4x4',
  ];

  const getWidget = () => {
    switch (item.type) {
      case 'component':
        switch (item.component) {
          case 'iframe':
            appLayoutList = Iframe.appLayoutList;
            return <Iframe item={item} />;
          case 'calendar':
            appLayoutList = Calendar.appLayoutList;
            return <Calendar item={item} />;
          case 'notes':
            appLayoutList = Notes.appLayoutList;
            return <Notes item={item} />;
          case 'todo':
            appLayoutList = Todo.appLayoutList;
            return <Todo item={item} />;
          case 'blog':
            appLayoutList = Blog.appLayoutList;
            return <Blog item={item} />;
          case 'fishingSingle':
            appLayoutList = GameFishingSingle.appLayoutList;
            return <GameFishingSingle item={item} />;
          case 'game_jzq':
            appLayoutList = GameJzq.appLayoutList;
            return <GameJzq item={item} />;
          case 'game_xmlk':
            appLayoutList = GameXmlk.appLayoutList;
            return <GameXmlk item={item} />;
          case 'game_2048':
            appLayoutList = Game2048.appLayoutList;
            return <Game2048 item={item} />;
          case 'game_ball_weighing':
            appLayoutList = GameBallWeighing.appLayoutList;
            return <GameBallWeighing item={item} />;
          case 'cfs_animate':
            appLayoutList = CfsAnimate.appLayoutList;
            return <CfsAnimate item={item} />;
          case 'app_noise':
            appLayoutList = Noise.appLayoutList;
            return <Noise item={item} />;
          case 'app_tongji':
            appLayoutList = TongJi.appLayoutList;
            return <TongJi item={item} />;
          case 'app_tianditu':
            appLayoutList = TianDiTu.appLayoutList;
            return <TianDiTu item={item} />;
          case 'app_files':
            appLayoutList = Files.appLayoutList;
            return <Files item={item} />;
          case 'timermark':
            appLayoutList = TimerMark.appLayoutList;
            return <TimerMark item={item} />;
          case 'r3f-demo':
            appLayoutList = R3fDemo.appLayoutList;
            return <R3fDemo item={item} />;
          default:
            appLayoutList = App.appLayoutList;
            return <App item={{ type: 'url', iconText: '暂不支持' }} />;
        }
      case 'url':
      default:
        appLayoutList = App.appLayoutList;
        return <App item={item} />;
    }
  };

  const renderWidget = getWidget();
  const dropdownMenus = [
    {
      name: '布局',
      icon: 'LayoutOutlined',
      items: appLayoutList.map((size: any) => {
        return {
          name: size,
          onClick: () => updateDate && updateDate('app.update', { ...item, size }),
        };
      }),
    },
    // {
    //   name: '编辑',
    //   icon: 'EditOutlined',
    //   onClick: () => message.info('暂不支持')
    // },
    // {
    //   name: '删除',
    //   icon: 'DeleteOutlined',
    //   onClick: () => message.info('暂不支持')
    // },
  ];
  return (
    <ContextMenu menus={dropdownMenus}>
      <div
        className={classnames(styles.appWidget, styles[`appWidget-w-${w}`], styles[`appWidget-h-${h}`])}
      >
        <>
          <div className={styles.appAvatar}>
            {renderWidget}
          </div>
          {item.name && <div className={styles.appTitle}>{item.name}</div>}
        </>
      </div>
    </ContextMenu>
  );
};

export default Widget;
