import React from 'react';
import { TAppItem } from '@/routes/type';
import Popup from '@/component/Popup';
import { appLayoutList } from './const';
import App from './app';

type IProps = {
  item: TAppItem;
};

const Notes = ({
  item
}: IProps) => {
  const renderPanel = (options: any) => {
    return <div></div>;
  };
  return (
    <Popup
      width={1024}
      panel={({ onClose }: any) => renderPanel({ onClose })}
    >
      <App item={item} />
    </Popup>
  );
};

Notes.appLayoutList = appLayoutList;

export default Notes;
