import React from 'react';
import classnames from 'classnames';
import { ScheduleOutlined, DoubleRightOutlined } from '@ant-design/icons';
import { TAppItem } from '@/routes/type';
import { appLayoutMap } from './const';
import App from '../App';
import styles from './app.less';

type IProps = {
  item: TAppItem;
  onClick?: any;
};

const View = ({
  item,
  onClick,
}: IProps) => {
  switch (item.size) {
    case appLayoutMap['1x1']:
    case appLayoutMap['1x2']:
    case appLayoutMap['2x1']:
    case appLayoutMap['2x2']:
    case appLayoutMap['2x4']:
    default:
      return <App item={item} onClick={onClick} />;
  }
};

export default View;
