import React from 'react';
import { Menu } from 'antd';
import type { MenuProps } from 'antd';
import Icon from '@/component/system/Base/Icon';
import { guid } from '@/utils/tool';
import {
  defaultUrlsList, myBooksList, myGameList, myWidgetList, myWorkList
} from './constants';
import styles from './index.less';

const getMenuItems = (items: any[]) => {
  return items.map(item => {
    if (item?.type === 'divider') {
      return item;
    }
    const _items: any = { key: guid() };
    if (item?.type === 'group') {
      _items.label = <span style={{ fontSize: 11 }}>{item.name}</span>;
      _items.type = item.type;
    } else {
      _items.label = <span style={{ fontSize: 13 }}>{item.name}</span>;
    }
    let icon = item.src || item.icon;
    let iconColor = item?.backgroundColor;
    if (item?.type === 'url') {
      icon = 'LinkOutlined';
      iconColor = '';
    }
    if (icon) {
      _items.icon = (
        <Icon
          name={icon}
          size={14}
          style={{
            // background: item?.backgroundColor,
            color: iconColor,
            borderRadius: '2px',
            objectFit: 'cover',
            marginRight: '8px',
          }}
        />
      );
    }
    if (item.items) {
      _items.children = getMenuItems(item.items);
    }
    return _items;
  });
};

const DropdownContent = () => {
  const items: MenuProps['items'] = getMenuItems([
    {
      name: '关于 ElevenOS',
    },
    {
      name: <div style={{ width: 148 }}>系统偏好设置</div>,
    },
    {
      type: 'divider',
    },
    {
      name: '应用菜单',
      type: 'group',
      items: [
        // ...myWorkList,
        ...myWidgetList,
        ...myGameList,
        ...myBooksList,
      ],
    },
    // {
    //   name: '网站聚合',
    //   type: 'group',
    //   items: defaultUrlsList,
    // },
  ]);

  const onClick: MenuProps['onClick'] = e => {
    console.log('click', e);
  };

  return (
    <div className={styles.dropdownContent}>
      <Menu onClick={onClick} mode="vertical" items={items} theme={'dropdownMenuPopup' as any} />
    </div>
  );
};

export default DropdownContent;
