import React, { useState, useEffect } from 'react';
// import { ThunderboltFilled, AppstoreFilled } from '@ant-design/icons';
import classnames from 'classnames';
import { TAppList, TAppItem } from './type';
// import Search from '@/component/Search';
import Time from '@/component/Time';
import Tips from '@/component/Tips';
import Apps from '@/component/Apps';
// import Apps from '@/component/SortableApps';
import ContextMenu from '@/component/ContextMenu';
import { defaultAppsList, myWorkList, myBooksList, myWidgetList } from './constants';
import {
  // DB_APPS_LIST,
  // IS_DEV,
  DB_MINIMALISM
} from '@/constants';
import storage from '@/utils/storage';
import { getQuery } from '@/utils/tool';
import styles from './app.less';

const App: React.FC = () => {
  const { type } = getQuery();
  // const AppsList: TAppList[] = IS_DEV ? defaultAppsList : storage.get(DB_APPS_LIST) || defaultAppsList;
  const AppsList: TAppList[] = [
    ...((type === 'xhzy' && myWorkList) || []),
    ...myWidgetList,
    ...myBooksList,
    {type: 'divider'},
    ...defaultAppsList,
  ];
  const [appsList, setAppsList] = useState<TAppList[]>(AppsList.map((i: any, index) => {
    i.index = index;
    i.items = (i.items || []).map((o: any, oindex: any) => {
      o.index = oindex;
      o.pindex = index;
      return o;
    });
    return i;
  }));

  const updateAppsList = (action: string, value: TAppItem) => {
    let nextAppsList = appsList;
    if (action === 'app.update') {
      nextAppsList = appsList.map((i: any) => {
        i.items = (i.items || []).map((o: any) => {
          if (value.pindex === i.index && value.index === o.index) {
            return value;
          }
          return o;
        });
        return i;
      });
    }
    setAppsList(nextAppsList);
    // storage.set(DB_APPS_LIST, nextAppsList);
  };

  const [isMinimalism, setIsMinimalism] = useState<boolean>(!!storage.get(DB_MINIMALISM, 0));

  const handleSwitchMinimalism = () => {
    storage.set(DB_MINIMALISM, !isMinimalism);
    setIsMinimalism(!isMinimalism);
  };

  return (
    <ContextMenu>
      <div className={classnames(styles.homePage, isMinimalism && styles.isMinimalism)}>
        <div className={styles.homePageHeaderWrap} />
        <div className={styles.homePageDateWrap}><Time isMinimalism={isMinimalism} /></div>
        {/* <div className={styles.homePageSearchWrap}><Search isMinimalism={isMinimalism} /></div> */}
        <div className={styles.homePageAppGridWrap}>
          {!isMinimalism && (<Apps appsList={appsList} updateDate={updateAppsList} />)}
        </div>
        <div className={styles.homePageTipsWrap}><Tips /></div>
        {/* <div className={styles.homePageTopButton}>
          <div className={styles.item} onClick={handleSwitchMinimalism}>
            {isMinimalism ? <AppstoreFilled /> : <ThunderboltFilled />}
          </div>
        </div> */}
      </div>
    </ContextMenu>
  );
};

export default App;
