import React from 'react';
import { Dropdown } from 'antd';
import classnames from 'classnames';
import styles from './index.less';

const TaskbarButton = ({
  children,
  className,
  dropdown,
}: any) => {
  const _button = (
    <div className={classnames(styles.taskbarButton, className)}>
      {children}
    </div>
  );
  if (dropdown) {
    return (
      <Dropdown
        trigger={dropdown.trigger || ['click']}
        dropdownRender={dropdown.dropdownRender ? () => (
          <div className={classnames(styles.dropdownRender, dropdown.className)}>
            {dropdown.dropdownRender}
          </div>
        ) : () => undefined}
      >
        <a onClick={e => e.preventDefault()}>
          {_button}
        </a>
      </Dropdown>
    );
  }
  return _button;
};

export default TaskbarButton;
