import React from 'react';
import classnames from 'classnames';
import { CalendarOutlined } from '@ant-design/icons';
import { TAppItem } from '@/routes/type';
import { appLayoutMap } from './const';
import { formatDate } from './tool';
import App from '../App';
import styles from './app.less';

type IProps = {
  item: TAppItem;
  onClick?: any;
};

const Calendar = ({
  item,
  onClick,
}: IProps) => {
  if (!item.url) {
    onClick = null;
  }
  const { year, month, day, days } = formatDate(item.componentData);
  const {
    name = '',
    options = {
      // bgColor: 'rgba(125, 171, 136, 1)',
      // bgImage: 'https://static.wetab.link/widget-background/background03_small.jpg',
      // // bgMask: 43,
      // bgType: 'color',
      // fontColor: 'rgba(248, 248, 248, 1)',
    },
  } = item.componentData;

  // 处理背景 和 文字
  const themeStyle: any = {};
  if (options.bgType === 'image') {
    themeStyle.backgroundColor = options.bgColor || 'rgba(125, 171, 136, 1)';
    themeStyle.backgroundImage = `url(${options.bgImage})`;
  } else {
    themeStyle.backgroundColor = options.bgColor || 'rgba(125, 171, 136, 1)';
    themeStyle.backgroundImage = 'none';
  }
  themeStyle.color = options.fontColor || '#fff';
  const maskStyle: any = {};
  if (options.bgMask) {
    maskStyle.backgroundColor =`rgba(0,0,0,${options.bgMask / 100})`;
  }

  switch (item.size) {
    case appLayoutMap['1x1']:
      return (
        <div className={classnames(styles.container)} style={themeStyle} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles.column, styles['jc-center'])} style={maskStyle}>
            <div
              className={classnames(styles.f12)}
              style={{ padding: '0 8px', textAlign: 'center' }}
            >
              {name}
            </div>
            <div className={classnames(styles.f24)}>{days}</div>
          </div>
        </div>
      );
    case appLayoutMap['1x2']:
      const _item: any = {
        type: 'component',
        size: '1x2',
        iconCode: item.iconCode,
        backgroundColor: item.backgroundColor,
        name: `${days} 天`,
        tagName: name,
        color: '#fff',
        tagColor: item.tagColor,
        themeStyle,
      };
      return (
        <App item={_item} onClick={onClick} />
      );
    case appLayoutMap['2x1']:
      return (
        <div className={classnames(styles.container, styles.row)} style={themeStyle} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles.column, styles['jc-evenly'])} style={maskStyle}>
            <div
              className={classnames(styles.f12)}
              style={{ lineHeight: '15px', height: '15px', padding: '0 8px', textAlign: 'center' }}
            >
              {name}
            </div>
            <div className={classnames(styles.f36)}>{days}</div>
            <div className={classnames(styles.f12)}>{`${month}-${day}`}</div>
          </div>
        </div>
      );
    case appLayoutMap['2x2']:
    default:
      return (
        <div className={classnames(styles.container)} style={themeStyle} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles['jc-evenly'], styles['ac-start'])} style={maskStyle}>
            <div
              className={classnames(styles.f12)}
              style={{ padding: '0 8px', textAlign: 'center' }}
            >
              {name}
            </div>
            <div>
              <span className={classnames(styles.f36)}>{days}</span>
              <span className={classnames(styles.f12)} style={{ marginLeft: '2px' }}>天</span>
            </div>
            <div className={classnames(styles.f12)}>{`${year}-${month}-${day}`}</div>
          </div>
        </div>
      );
  }
};

export default Calendar;
