import React from 'react';
import classnames from 'classnames';
import { TAppItem } from '@/routes/type';
import { appLayoutMap } from './const';
import App from '../App';
import MicroPanel from '@/component/Popup/panel/micro';
import { getMicroAppInfo, IMicroAppInfo } from '@/microApp';
import styles from './app.less';

type IProps = {
  item: TAppItem;
  onClick?: any;
};

const R3fDemo = ({
  item,
  onClick,
}: IProps) => {
  const microAppKey = 'app_r3f';
  const microAppInfo: IMicroAppInfo | null = getMicroAppInfo(microAppKey);

  microAppInfo.props = {
    // showType: 'random'
    showType: 'demo4'
  };

  const _item: any = {
    type: 'component',
    name: '3D 模型',
    iconCode: 'CodeSandboxOutlined',
    backgroundColor: '#383948',
    tagName: 'R3F 实例',
    tagColor: '#383948',
  };

  switch (item.size) {
    case appLayoutMap['1x1']:
    case appLayoutMap['1x2']:
      _item.size = item.size;
      return (
        <App item={_item} onClick={onClick} />
      );
    default:
      return (
        <App item={item}>
          <div className={styles.container}>
            <MicroPanel microAppInfo={microAppInfo} id="micro-app-container-r3f" />
          </div>
        </App>
      );
  }
};

export default R3fDemo;
