import React from 'react';
import TaskbarButton from '@/component/system/Taskbar/TaskbarButton';
import { BatteryIcon, ChargeIcon } from '@/component/system/Taskbar/BatteryButton/Icon';
import DropdownContent from '@/component/system/Taskbar/BatteryButton/dropdown';
import useBattery from '@/hooks/useBattery';
import styles from './index.less';

const BatteryButton = () => {
  const batteryState = useBattery();
  const color = (): string => {
    if (batteryState.charging) return '#52c41a';
    if (batteryState.level < 0.2) return '#ff4d4f';
    if (batteryState.level < 0.5) return '#faad14';
    return 'currentColor';
  };

  return (
    <TaskbarButton
      dropdown={{
        dropdownRender: <DropdownContent data={batteryState} />
      }}
    >
      <div className={styles.batteryButton}>
        <BatteryIcon />
        <div
          className={styles.batteryLevel}
          style={{
            width: `${(11.5 * batteryState.level).toFixed(2)}px`,
            background: color(),
          }}
        />
        {batteryState.charging && (
          <div className={styles.batteryCharging}>
            <ChargeIcon />
          </div>
        )}
      </div>
    </TaskbarButton>
  );
};

export default BatteryButton;
