import React from 'react';
import { TAppItem } from '@/routes/type';
import Popup from '@/component/Popup';
import MicroPanel from '@/component/Popup/panel/micro';
import { getMicroAppInfo, IMicroAppInfo } from '@/microApp';
import { appLayoutList } from './const';
import App from './app';


type IProps = {
  item: TAppItem;
};

const Files = ({
  item
}: IProps) => {
  const microAppKey = 'app_files';
  const microAppInfo: IMicroAppInfo | null = getMicroAppInfo(microAppKey);
  return (
    <Popup
      width={1024}
      height={576}
      panel={() => <MicroPanel microAppInfo={microAppInfo} />}
      link={microAppInfo?.entry}
      needTrack
    >
      <App item={item} />
    </Popup>
  );
};

Files.appLayoutList = appLayoutList;

export default Files;
