import React, { useEffect, useState } from 'react';
import { loadMicroApp } from 'qiankun';
import { Spin, Result, Typography } from 'antd';
import { IMicroAppInfo } from '@/microApp';
import Orbit from '@/component/Orbit';

type IProps = {
  id?: string;
  microAppInfo: IMicroAppInfo | null;
}

const Panel = ({
  microAppInfo,
  id = 'micro-app-container',
}: IProps) => {
  let microApp: any = null;
  const [initLoading, setInitLoading] = useState(true);
  const [isError, setIsError] = useState(false);

  // 处理参数透传问题 props 字段会被合并掉
  let _microAppInfo: any = {};
  if (microAppInfo?.props) {
    const _props = {...microAppInfo?.props};
    delete microAppInfo?.props;
    _microAppInfo = {
      ...microAppInfo,
      props: { props: _props },
      container: `#${id}`,
    };
  } else {
    _microAppInfo = {
      ...microAppInfo,
      container: `#${id}`,
    };
  }

  useEffect(() => {
    if (microAppInfo) {
      microApp = loadMicroApp(_microAppInfo, {
        // 部分第三方动态加载的资源（css/js) 不被劫持处理
        excludeAssetFilter: (assetUrl: string) => {
          const url = assetUrl.replace(/^http(s)?:\/\//, '');
          if (!(
            url.startsWith(window.location.hostname) ||
            url.startsWith('localhost')
          )) {
            console.log('assetUrl', assetUrl);
            return true;
          }
          return false;
        }
      });
    }
    if (microApp) {
      microApp.mountPromise.then(() => {
        setInitLoading(false);
      }).catch((error: any) => {
        setInitLoading(false);
        setIsError(true);
      });
    }
    return () => {
      if (microApp) {
        microApp.mountPromise.then(() => {
          microApp.unmount();
        }).catch(() => { });
      }
    };
  }, []);

  return (
    <Spin spinning={initLoading} indicator={<Orbit text='加载中' />}>
      <div id={id} style={{ display: !initLoading && !isError ? 'block' : 'none' }}></div>
      {!initLoading && isError && (
        <div
          style={{
            display: 'flex',
            height: '100%',
            justifyContent: 'center',
            alignItems: 'center',
            flexWrap: 'nowrap',
          }}
        >
          <Result
            status="error"
            title="抱歉，应用加载失败"
            subTitle={(
              <span>请检查应用是否可运行 <Typography.Link href={microAppInfo?.entry} target="_blank">{microAppInfo?.entry}</Typography.Link></span>
            )}
          >
          </Result>
        </div>
      )}
    </Spin>
  );
};

export default Panel;
