import React from 'react';
import TaskbarButton from '@/component/system/Taskbar/TaskbarButton';
import { OnlineIcon, OfflineIcon } from '@/component/system/Taskbar/WifiButton/Icon';
import DropdownContent from '@/component/system/Taskbar/WifiButton/dropdown';
import useNetwork from '@/hooks/useNetwork';

const WifiButton = () => {
  const networkState = useNetwork();
  const { online } = networkState;
  return (
    <TaskbarButton
      dropdown={{
        dropdownRender: <DropdownContent data={networkState} />
      }}
    >
      {online ? <OnlineIcon /> : <OfflineIcon />}
    </TaskbarButton>
  );
};

export default WifiButton;
