import React from 'react';
import { TAppItem } from '@/routes/type';
import Popup from '@/component/Popup';
import IframePanel from '@/component/Popup/panel/iframe';
import { appLayoutList } from './const';
import App from './app';

type IProps = {
  item: TAppItem;
};

const TimerMark = ({
  item
}: IProps) => {
  if (item.componentData.url) {
    item.url = item.componentData.url;
  }
  return (
    <Popup
      width={1024}
      panel={() => <IframePanel item={item} />}
      link={item.url}
    >
      <App item={item} />
    </Popup>
  );
};

TimerMark.appLayoutList = appLayoutList;

export default TimerMark;
