import React from 'react';
import classnames from 'classnames';
import type { DropDownProps, MenuProps } from 'antd';
import { Dropdown } from 'antd';
import Icon from '@/component/Icon';
import styles from './index.less';

type TMenuItem = {
  name?: string;
  icon?: string;
  onClick?: () => void;
  items?: {
    name?: string;
    onClick?: () => void;
  }[];
};

interface IProps extends DropDownProps {
  menus?: TMenuItem[];
};

const ContextMenu = ({
  menus = [],
  ...props
}: IProps) => {
  props = {
    getPopupContainer: triggerNode => triggerNode.parentNode as any,
    ...props,
    trigger: ['contextMenu'],
    overlayClassName: classnames(styles.contextMenu, menus.length === 0 && styles.disable, props.overlayClassName),
    menu: { items: [] },
  };
  if (menus.length > 0) {
    const items: MenuProps['items'] = menus.map((item, index) => {
      const _item: any = { key: index };
      if (item.name || item.icon) {
        _item.label = (
          <div key={index} className={classnames(styles.dropdownMenu, item.onClick && styles.action)} onClick={item?.onClick}>
            <div className={styles.dropdownMenuInfo}>
              {item.name && <div className={styles.dropdownMenuName}>{item.name}</div>}
              {item.icon && <Icon name={item.icon} className={styles.dropdownMenuIcon} />}
            </div>
            {item.items?.length && (
              <div className={styles.dropdownSubMenu}>
                {item.items?.map((o, oindex) => (
                  <div key={oindex} className={styles.dropdownSubMenuItem} onClick={o.onClick}>
                    {o.name}
                  </div>
                ))}
              </div>
            )}
          </div>
        );
      }
      return _item;
    });
    props.menu = {
      items,
    };
  }

  return (
    <Dropdown {...props} />
  );
};

export default ContextMenu;
