import React from 'react';
import { TAppItem } from '@/routes/type';
import Popup from '@/component/Popup';
import IframePanel from '@/component/Popup/panel/iframe';
import { appLayoutList } from './const';
import App from '../App/app';

type IProps = {
  item: TAppItem;
};

const Iframe = ({
  item
}: IProps) => {
  return (
    <Popup
      width={1024}
      panel={() => <IframePanel item={item} />}
      link={item.url}
    >
      <App item={item} />
    </Popup>
  );
};

Iframe.appLayoutList = appLayoutList;

export default Iframe;
