import React from 'react';
import classnames from 'classnames';
import { BookOutlined, DoubleRightOutlined } from '@ant-design/icons';
import { TAppItem } from '@/routes/type';
import { appLayoutMap } from './const';
import styles from './app.less';

type IProps = {
  item: TAppItem;
  onClick?: any;
};

const App = ({
  item,
  onClick,
}: IProps) => {
  const count = 3;
  const total = 5;
  /**
   * 渲染小组件面板
   */
  const renderNotesPanel = () => {
    const items: any[] = new Array(count).fill('');
    return (
      <div className={styles.notesPanel}>
        {item.size === appLayoutMap['2x4'] && (
          <div className={classnames(styles.notesTitle, styles['tc-theme-color'])}>
            新建笔记 <DoubleRightOutlined />
          </div>
        )}
        <div className={classnames(styles.notesList)}>
          {items.map((i, index) => (
            <div key={`i-${index}`} className={styles.notesItem}>
              <span className={classnames(styles.tag)} />
              <div className={classnames(styles.info)}>
                <div className={classnames(styles.title)}>表其他</div>
                <div className={classnames(styles.date)}>05月04日</div>
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  };

  switch (item.size) {
    case appLayoutMap['1x1']:
      return (
        <div className={classnames(styles.container)} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles.column, styles['jc-center'])}>
            <div className={classnames(styles.f24)}>{total}</div>
            <div className={classnames(styles.f12, styles['tc-theme-color'])}>笔记</div>
          </div>
        </div>
      );
    case appLayoutMap['1x2']:
      return (
        <div className={classnames(styles.container)} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles.row, styles['jc-evenly'])}>
            <div className={classnames(styles.f24)}>{total}</div>
            <div className={classnames(styles.f16, styles['tc-theme-color'])}>笔记</div>
          </div>
        </div>
      );
    case appLayoutMap['2x1']:
      return (
        <div className={classnames(styles.container, styles.row)} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles.column, styles['jc-evenly'])}>
            <div className={classnames(styles.f12)} style={{ lineHeight: '15px', height: '15px' }}></div>
            <div className={classnames(styles.f36)}>{total}</div>
            <div className={classnames(styles.f12, styles['tc-theme-color'])}>笔记</div>
          </div>
        </div>
      );
    case appLayoutMap['2x4']:
      return (
        <div className={classnames(styles.container)} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles.column, styles.needSubPanel, styles['jc-between'])} style={{ width: '36%', padding: 8 }}>
            <div className={classnames(styles.iconWrap, styles['jc-end'])}>
              <BookOutlined />
            </div>
            <div className={classnames(styles.flex, styles['ai-baseline'], styles['tc-theme-color'], styles['w-full'], styles['jc-start'])}>
              <div className={classnames(styles.f36)} style={{ paddingRight: '4px' }}>{total}</div>
              <div className={classnames(styles.f12)}>条笔记</div>
            </div>
          </div>
          <div className={classnames(styles.subPanel)}>
            {renderNotesPanel()}
          </div>
        </div>
      );
    case appLayoutMap['2x2']:
    default:
      return (
        <div className={classnames(styles.container)} onClick={onClick}>
          <div className={classnames(styles.mainPanel, styles['fw-nowrap'], styles['ac-start'])}>
            <div className={classnames(styles.miniHeader)}>
              <div className={classnames(styles.f12)}>{`${total}条 笔记记录`}</div>
            </div>
            <div className={classnames(styles.flex, styles['ai-center'], styles['flex-grow'])}>
              {renderNotesPanel()}
            </div>
          </div>
        </div>
      );
  }
};

export default App;
