/**
 * 格式化时间
 */
export const formatDate = ({
  time,
  countdown,
}: {
  time: number,
  countdown: boolean,
}) => {
  const date = new Date(time);
  const curDate = new Date();
  const curTime = new Date(`${curDate.getFullYear()}-${curDate.getMonth() + 1}-${curDate.getDate()}`).getTime();
  return {
    year: date.getFullYear(), // 年
    month: `00${date.getMonth() + 1}`.substr(-2), // 月
    day: `00${date.getDate()}`.substr(-2), // 日
    days: Math.ceil((countdown ? (time - curTime) : (curTime - time)) / (24 * 60 * 60 * 1000))
  };
};
