import React from 'react';
import * as IconMap from '@ant-design/icons';

interface IProps {
  name: string;
  size?: number;
  style?: any;
  [key: string]: any;
};

const Icon = (props: IProps) => {
  const { name, size = 16, style } = props;
  let LegacyIcon = null;
  let LegacyProps = null;
  if (name.includes('/')) {
    LegacyProps = {
      style: {
        width: size,
        height: size,
        ...style,
      }
    };
    // 图片地址
    LegacyIcon = <img src={name} {...LegacyProps} />;
  }
  if ((IconMap as any)[name]) {
    const AntdIcon = (IconMap as any)[name];
    LegacyProps = {
      fontSize: size,
      style,
    };
    // AntD Icon
    LegacyIcon = <AntdIcon type={props.name} {...LegacyProps} />;
  }
  return LegacyIcon;
};

export default Icon;
