import React from 'react';
import * as IconMap from '@ant-design/icons';

interface IProps {
  name: string;
  [key: string]: any;
};

const Icon = (props: IProps) => {
  const LegacyIcon = (IconMap as any)[props.name];
  return LegacyIcon ? <LegacyIcon type={props.name} {...props} /> : null;
};

export default Icon;
