import React from 'react';
import classnames from 'classnames';
import { TAppItem } from '@/routes/type';
import { isLightColor } from '@/utils/tool';
import Icon from '@/component/Icon';
import styles from './index.less';
import { getSize } from '../tool';

type IProps = {
  item: TAppItem;
  onClick?: any;
  children?: any;
};

const Application = ({
  item,
  children,
  onClick
}: IProps) => {
  const { w, h } = getSize(item.size);
  let renderIcon = null;
  if (item.iconCode !== 'none') {
    if (item.src) {
      renderIcon = <span style={{ backgroundImage: `url(${item.src})` }} />;
    } else if (item.iconCode) {
      renderIcon = <Icon name={item.iconCode} />;
    } else {
      renderIcon = <span>{(item.iconText || item.name || '').slice(0, 1)}</span>;
    }
  }
  const coverStyle = item.backgroundColor ? { backgroundImage: `linear-gradient(45deg,#fff,${item.backgroundColor} 41%)` } : { backgroundColor: 'transparent' };
  const nameStyle = item.backgroundColor ? { color: isLightColor(item.backgroundColor || '#c7c7cc') ? '#000' : '#fff' } : { color: '#fff' };
  const iconStyle: any = w <= h ? {
    width: `calc(var(--app-size) * ${w} + var(--app-gap-y) * ${w - 1})`,
    height: `calc(var(--app-size) * ${h} + var(--app-gap-x) * ${h - 1})`,
  } : {
    width: 40 * h,
    height: 40 * h,
  };
  iconStyle.background = item.backgroundColor || 'transparent';
  if (item.themeStyle) {
    if (item.themeStyle?.backgroundColor || item.themeStyle?.backgroundImage) {
      coverStyle.backgroundColor = item.themeStyle?.backgroundColor || coverStyle?.backgroundColor;
      coverStyle.backgroundImage = item.themeStyle?.backgroundImage || coverStyle?.backgroundImage;
    }
    if (item.themeStyle?.color) {
      nameStyle.color = item.themeStyle?.color;
    }
  }
  let descStyle = {};
  if (item.tagColor === 'none') {
    descStyle = { color: nameStyle.color, backgroundColor: 'transparent' };
  } else {
    descStyle = item.tagColor ? { color: item.tagColor, backgroundColor: '#fff' } : {};
  }

  return (
    <div
      className={classnames(styles.appIcon, w <= h && styles.appTheme)}
      style={{
        color: item.color || (isLightColor(item.backgroundColor || '#c7c7cc') ? '#000' : '#fff')
      }}
      onClick={onClick}
    >
      {children ? children : (
        <>
          <div className={styles.cover} style={coverStyle} />
          <span className={styles.name} style={nameStyle}>{item.name}</span>
          <div className={styles.desc} style={descStyle}>{item.tagName}</div>
          {renderIcon && <div className={styles.icon} style={iconStyle}>{renderIcon}</div>}
        </>
      )}
    </div>
  );
};

export default Application;
