import React, { useEffect, useState } from 'react';
import { TAppItem, TAppList } from '@/routes/type';
import { DB_APPS_CUR_PANEL_INDEX, defaultAppsCurPanelIndex } from '@/constants';
import storage from '@/utils/storage';
import Bar from '../Bar';
import Widget from '../../widget';
import styles from './index.less';

type IProps = {
  appsList: TAppList[];
  updateDate?: (action: string, item: TAppItem) => void;
};

const Apps = ({
  appsList,
  updateDate
}: IProps) => {
  const curBarIndex = storage.get(DB_APPS_CUR_PANEL_INDEX) || defaultAppsCurPanelIndex;
  let carouselRef = React.createRef<any>();
  const [curItem, setCurItem] = useState<TAppList>(appsList[curBarIndex]);
  const handleClickItem = (item: any) => {
    setCurItem(item);
    // storage.set(DB_APPS_CUR_PANEL_INDEX, item.index);
  };

  useEffect(() => {
    carouselRef?.current?.goTo(curItem.index);
  }, [curItem, carouselRef]);

  const itemsCount = appsList.length;

  return (
    <>
      <div className={styles.appsWrap}>
        <div className={styles.appsPanel}>
          {itemsCount && (
            <div
              className={styles.appsSwiper}
              style={{
                height: `${itemsCount * 100}%`,
                transform: `translateY(-${100 / itemsCount * curItem.index}%)`
              }}
            >
              {appsList.map(apps => (
                <div className={styles.appSwiper} key={apps.index} style={{ height: `${100 / itemsCount}%` }}>
                  <div className={styles.appPanel}>
                    <div className={styles.appGrid}>
                      {apps?.items?.map((item: TAppItem) => <Widget item={item} key={item.index} updateDate={updateDate} />)}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
      <div className={styles.barWrap}>
        <Bar list={appsList} handleClickItem={handleClickItem} curItem={curItem} />
      </div>
    </>
  );
};

export default Apps;
